
<template>
    <div>
    <loader :is-visible="isLoading"></loader>
        <TgList IsShowAction="true"
                ModuleName="CRM" 
                SubModuleCode="CRM_CONTRACTS" 
                IdentityColumn="contract_id"
                :HeaderText="$t('Contract')"
                :ListData="ContractData" 
                :HeaderData="Headers"
                :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecords"
                :SearchFields="leftSearchFields"
                :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData"
                :LegendArray="LegendArray"
                :IsFilterApplied="searchCondition?true:false"
                :SortExp="SortExp"
                :SortBy="SortBy"
                :CurrentPageNumber="PageNumber"
                v-on:LeftsearchButtonClick="leftCommonSearch"
                v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:ActionButtonClick="actionButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{data}">
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <listing-status  :isfor="'CRM_CONTRACTS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_CONTRACTS')" ref="updatestatus" v-bind:dataid="data.row.contract_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index"  v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true"
                         >
                        </listing-status>
                        <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_CONTRACTS')">
                            <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                {{data.row[data.column.COLUMN_NAME]}}
                            </span>
                        </a>
                    </template>
                </template>
        </TgList>

    <searchfilter v-if="showFilter" submodulecode="CRM_CONTRACTS" modulename="CRM" v-on:close="showFilter = false" v-bind:callbackfunction="FetchData"  v-on:FilterData="FilterData">
        <h5 slot="header">{{$t('Search')}}</h5>
    </searchfilter>    
    <viewlayout 
        v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_CONTRACTS" modulename="CRM"
        v-on:close="ShowManageView = false" v-bind:callbackfunction="FetchData"
        :usertype="usertype">
        <h5 slot="header">{{ $t('Search') }}</h5>
    </viewlayout>

        <modalfollowupslist :srcId="souceID"  v-if="showAddEventModal"
        modulename="CRM" submodulecode="CRM_CAMPAIGNS" :userid="userid"
        v-on:close="closeEventViewModal">
    </modalfollowupslist>
    <ModelSendBulkEmail v-if="showAddEmailTemplate" @close="showAddEmailTemplate = false" callFrom="ContractDetails" :selectedids="modalItem" :submodulecode="$route.params.subModuleCode">
        <h5 class="modal-title" slot="header">{{$t('SendEmail')}}</h5>
    </ModelSendBulkEmail>
    </div>
    </template>

<script>
 import Schema from "../../CRM/Contract/ContractListingSchema";
    import ContractDataService from "../../../services/DataService";
    import searchfilter from '../../../components/Common/Modal/search-filter.vue'
    import viewlayout from '../../../components/Common/Modal/view-layout.vue'
    import modalfollowupslist from '../../../components/Common/Modal/modalfollowupslist.vue'
    import ModelSendBulkEmail from "../../../components/Common/Modal/sendbulkemail.vue";
    import TgList from '../../../components/Common/TgList.vue'
 
    export default {
    components: {
        TgList,
        ContractDataService,
        viewlayout,
        ModelSendBulkEmail,
        searchfilter,
        modalfollowupslist,
        
},
data() {
    return {
        isLoading: false, 
        listheaderbuttons: Schema.buttons,
        listActions: Schema.listActions,
        leftSearchFields: Schema.leftsearchSchema,
        ContractData: null,
        StatusListForListing: [],
        Headers: null,
        TotalRecords: 0,
        PageNumber: 1,
        PageSize: 10,
        SortBy: '',
        SortExp: '',
        PageSizeOption: null,
        CurrentPage: 1,
        TotalPages: 0,
        GroupStart: 1,
        GroupEnd: 3,
        Status: '',
        ModuleType: '',
        souceID: 0,
        showAddEventModal: false,
        StatusIds: '',
        langCode: 'EN',
        showAddEmailTemplate: false,
        noRecord: false,
        showFilter: false,
        searchCondition: '',
        IsAnySearch: false,
        ShowManageView: false,
        conditionForInstantSearch:"",
        userid: null,
        usertype:null,
        LegendArray: [],
        appliedFilter:[]
    }
},
created: function () {
    this.userid = this.GetUserInfo.ID;
            this.usertype = this.GetUserInfo.UserType;
    var ths = this;
    ths.getStatuslistForListing("CRM", "en");
    ths.FetchData();
    ths.LegendArray = [
            {
                class: 'bg-primary colorboxsquare',
                text: (ths.$t('CloseLeadLegend', ['Contract']))
            }
        ]
}, 
methods: {
    renderActions: function(row) {
        var self = this;
        let rtnActionArray = [];
        self.listActions.forEach(actionItem => {
            let action = {...actionItem};
            switch (action.title) {
                case 'View':
                action.isVisible = true;
                break;
                case 'Edit':
                action.isVisible =  (row.status_id != 'Closed')
                    break;
                case 'FollowUps':
                action.isVisible = row.status_id != 'Closed';
                    break;
                default:
                    action.isVisible = true;
                    break;
                
            }
            rtnActionArray.push(action);
        });
        return rtnActionArray;
    },
    clickEventOfColumn:function(row, column){
        if(column.COLUMN_NAME == 'contract_name'){
            this.$router.push({ path: '/CRM/ViewContractDetails/'+row.contract_id+'/CRM/CRM_CONTRACTS' });
        }
    },
    sendBulkEmail: function (event) {
        
                var ids = '';
                var val = '';
               
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").not(".CloseLeadInput").each(function () {
               
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                    ids = val;
                });
                if (ids == null) {
                   //ShowAlert("Please select atleast one group.", "warning", true, this.$t('Alert'));
                    return false;
                }
               
                this.modalItem = ids;
                this.showAddEmailTemplate = true;
            },
    Searchdata: function (event) {
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.conditionForInstantSearch = "";
            this.IsAnySearch = false;
            if ($.trim(this.contractName) != "") {
                this.IsAnySearch = true;
                this.conditionForInstantSearch += ($.trim(this.conditionForInstantSearch) != "") ? " AND" : "";
                this.conditionForInstantSearch += " CON.CONTRACT_NAME LIKE '%" + this.contractName + "%'";
            }
            this.FetchData();
    },
    getStatuslistForListing: function (StatusType, lang) {
        var vm = this;
        var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
        ContractDataService.statusdropdown(url).then(function (response) {
            if (response.data.status == "Success") {
                var json = response.data.result;
                vm.StatusListForListing = json.data;
            }
        });
    },
    leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
    },
    actionButtonClick: function (callbackfunction, item, index, event) {
        var vm = this;
        switch (callbackfunction) {
            case 'checkPrivilegeView':
                break;
            case 'checkPrivilegeDetails':

                break;
            case 'openNoteListPopUp':
                vm.openNoteListPopUp(item, index);
                break;
            case 'openAddNewEvent':
                vm.openAddNewEvent(item,item.contract_id);
                break;
        }
    },
    sortdata: function (s) {
      var SortDir = "sort tb_headerSortDown";
       this.SortBy = s;
            if (s === this.SortBy) {
            if (this.SortExp == "") {
                this.SortExp = "ASC";
                SortDir = "sort tb_headerSortDown";
            }
            else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
                SortDir = "sort tb_headerSortUp";
            }
            else
            {
                this.SortExp = "ASC";
                SortDir = "sort tb_headerSortDown";
            }
            }
           
            this.FetchData();
            $("#" + s+'  span:first-child').attr("class", SortDir);
    },
    ManageView: function () {
        var vm = this;
        this.ShowManageView = true;
        setTimeout(function () {
            $("#modalManageView").modal('show');
            setTimeout(function(){vm.SetBootstraptoolTip()}, 200);
        }, 1000);
    },
    openAddNewEvent(item, source_id) {
        var vm = this;
        vm.souceID = source_id;
        vm.selectedItem = item;
        vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
    },
    closeEventViewModal: function () {
        this.showAddEventModal = false;
    },
    pagerMethod: function (value) {

        this.PageSize = value.PageSize;
        this.PageNumber = value.PageNumber;
        this.FetchData();
    },
    getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition,contract_Name) {
        let params = {};
        if (pageNum) { params["pageNum"] = pageNum; }
        if (searchcondition) { params["searchcondition"] = searchcondition; }
        if (isPaged) { params["isPaged"] = isPaged; }
        if (sortBy) { params["sortBy"] = sortBy; }
        if (sortExp) { params["sortExp"] = sortExp; }
        if (pageSize) { params["pageSize"] = pageSize; }
        if(contract_Name){params["contract_Name"]=contractName;}
        return params;
    },
    commonHeaderButtonClick: function (event) {
        switch (event.callbackfunction) {
            case "downloadExceldata":
                this.FetchData(true);
                break;
            case "openFilterPopUp":
                this.openFilterPopUp();
                break;
            case "ManageView":
                this.ManageView();
                break;
                case "sendBulkEmail":
                this.sendBulkEmail();
                break;
            case "commonDeleteData":
                this.commonDeleteData();
                break;
            case "removeFilter":
                var ths = this;
                this.searchCondition = '';
                const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';});
                ths.listheaderbuttons[removeFilterindex].isvisible=false;
                const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';});
                ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                 try {
                    ths.appliedFilter = [];
                 } catch (e) { };
                 ths.FetchData();
                break; 
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0,this);
                        break;
        }
    },    
    openFilterPopUp: function (source_id) {
        this.cancelOrRefresh();
        this.showFilter = true;
    },
    cancelOrRefresh: function () {
        this.clientemail = "";
        this.selectedLeadOwner = "";
        this.NameSearch = "";
        this.CompanyName = "";
        this.hdnOwnerId = "";
        this.conditionForInstantSearch = "";
        this.IsAnySearch = false;
        this.contractName="";
        this.FetchData();
    },
    FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData,contract_Name) {
        this.pageNum = pageNum;
        this.pageSize = pageSize;
        this.sortBy = sortBy;
        this.sortExp = sortExp;
        this.contract_Name=contract_Name
        this.searchCondition = searchcondition;
        if (typeof filterItemData != null)
            this.appliedFilter.push(filterItemData);
        this.FetchData();
    },
    commonDeleteData: function (event) {
        var vm = this;
        var val = "";
        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
            if (val.length > 0)
                val += ',';
            val += $(this).val();
        });
        if (val.length > 0) {
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Contract")), function () {
                var postJSON = {
                    ids: val,
                    moduleName: "CRM",
                    subModuleCode: "CRM_CONTRACTS"
                };
                var objectPostString = JSON.stringify(postJSON);
                ContractDataService.CommonDeleteData(objectPostString).then(function (response) {
                    var box = "";
                    if (response.status == 200) {
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                         $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $("#DeleteMultiple").addClass("disabled");
                        box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Contract')]), "Success", true, vm.$t("Alert"));
                        vm.PageNumber=parseInt(vm.PageNumber)-1;
                            if(vm.PageNumber<=0){
                                vm.PageNumber=1;
                            } 
                            vm.FetchData(); 
                    }
                    else {
                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Contract')]), "Success", true, vm.$t("Alert"));
                    }
                     
                },);
            });
        }
        else {
            ShowAlert(vm.$t("Selectanyrecordtodelete"));
        }
    },
    FetchData: function (downloadExceldata) {
        
        const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition,this.contract_Name);
        var vueObj = this;
        this.isLoading = true;
        if(downloadExceldata){
            params.pageSize = vueObj.TotalRecords;
        }else{
            params.pageSize = vueObj.PageSize;
        }
        params.pageNumber = vueObj.PageNumber;
        params.langCode = vueObj.langCode;
        params.searchCondition = vueObj.searchCondition;
        params.contract_Name=vueObj.contractName;
        if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
        else { vueObj.PageNumber = params.pageNum = 1; }
        params.sortBy = vueObj.SortBy;
        params.sortExp = vueObj.SortExp;
        var srchCondition = "";
        if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
        if (!this.isBlank(this.conditionForInstantSearch)) {
            srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
        }
        if (srchCondition != "") {
           params.searchCondition = srchCondition; 
                if(vueObj.appliedFilter.length>0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
            }
        }
        else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
          }
        if (downloadExceldata) {
            params.isPaged = 0;
            ContractDataService.ContractListing(params).then(function (response) {
                if (response.data != "") {
                    vueObj.JSONToCSVConvertor(response.data, "Contract-Detail", true);
                }
                else {
                    vueObj.ModelnoRecord = true;
                }
                vueObj.isLoading = false;
            });
        }
        else {
            ContractDataService.ContractListing(params).then(function (response) {
                if (response.data != null) {
                    vueObj.Headers = response.data.schema;
                    vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'contract_name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                    if (response.data.data.length > 0) {
                     
                        vueObj.ContractData = response.data.data;
                        vueObj.ContractData.forEach((row)=>{
                            row.isCheckBoxDisabled=false;
                                row.additionalClass='';
                                if(row.status_id=='Closed'){
                                    row.isCheckBoxDisabled=true;
                                    row.additionalClass='medium-bar';
                                }
                            row.rowDetailsUrl='';
                            if(row.rowDetailsUrl=='')
                            {
                                row.rowDetailsUrl='/CRM/ViewContractDetails/'+row.contract_id+'/CRM/CRM_CONTRACTS';
                            }
                            });
                        vueObj.TotalRecords = response.data.data[0].total_records;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $('#DeleteCampaign').addClass('disabled');
                        $('#Sendbulkmail').addClass('disabled');
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.ContractData = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.ContractData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                vueObj.isLoading = false;
            });


        }
    },
},
updated: function () {
    this.BindActionButtonEvent();
},
}
</script>