
<script>
    import DataService from "../../../services/DataService";
    export default {
        props: {
      selectedids:null,
        userid: {
            type: Number,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        templatename: {
            type: String,
            required: true
        },
        selecteditems: {
            type: Array,
            required: true
        },
        emailSubject: {
            type: String,
            required: true
        },
      
        submodulecode: {
            type: String,
            required: true
        },
        callFrom: {
            type: String
        }
    },
    data: function() {
        return {

            emailSubject: '',
            emailPriorityValue: '',
            duedate: '',
            Subject: '',
            txtemailDescription: "",
            emailtemplate: [],
            Message: '',
            emailtemplateValue: '',
            modal: [],
            isView: false,
            editor: this.CLASSIC_EDITOR,
            options: {
               
                useCurrent: false,
                showClear: true,
                showClose: true,
            },
        }
    },
    created: function () {
        this.modal = this.modalItem;
        this.GetEmailTemplate();
    },
    methods: {
        onReady( editor )  {
    editor.ui.getEditableElement().parentElement.insertBefore(
            editor.ui.view.toolbar.element,
            editor.ui.getEditableElement()
    );
  },
  
        GetEmailTemplate: function () {
          
            var vm = this;
            var url = "id=&type=EMAILTEMPLATE";
            if (vm.submodulecode == 'CRM_STAGES') {
                url='';
                 url = "id=&type=CRM_STAGE_EMAILTEMPLATE";
            }
             
            DataService.getLeaOwnerBySearch(url).then(function (response) {
                var json = response.data;
                 
                if (json != null) {     
                    vm.emailtemplate = json.DATA;              
                    return vm.emailtemplate;
                }
                 
            });
                
        },
        loadEmailTemplate: function (value) {
            if (value != '') {
                var vm = this;
                var templateID = value
                var url = "id=" + templateID;
                 
                DataService.GetEmailData(url).then(function (response) {
                        var json = response.data;
                         
                        if (json != null) {
                            vm.Description=json.data[0].template_content;
                        }
                         
                    });
            }
            else {
                vm.txtemailDescription = "";
            }
        },
        getSelectedValue: function (event) {
            this.emailtemplateValue = event.target.value;
            this.loadEmailTemplate(this.emailtemplateValue);
        },
        saveEmail: function (values,meta) {
            var vm = this;
            // if(vm.callFrom=='CampaignDetails')
            // vm.$refs.form.setFieldTouched('Description',true);  
            // vm.$refs.form.setFieldTouched('emailPriorityValue', true);  
            // vm.$refs.form.setFieldTouched('emailSubject', true);
            // vm.$refs.form.setFieldTouched('emailtemplateValue', true);
            vm.$refs.form.validate().then((result) =>{
          
                if (result) {
                    var data = {
                        templatename: vm.emailtemplateValue,
                        selecteditems: vm.selectedids,
                        emailSubject: vm.emailSubject,
                        emailPriorityValue: vm.emailPriorityValue,
                        submodulecode: vm.submodulecode,
                        mailStatus: 0,
                        emailCc: null,
                        emailBcc: null,
                        body:vm.callFrom=='CampaignDetails'?null: vm.Description,
                        to: null
                    };
                   
                    if(vm.callFrom=='CampaignDetails'){
                        data.campaignId=vm.$route.params.id;
                         var objectPostString = JSON.stringify(data);
                        DataService.AddGroupEmailTemplate(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t("Email Sent Successfully"),'success',true,vm.$t('Alert'));
                            vm.duedate = "";
                            vm.Subject = "";
                            vm.priority = "";
                            vm.Message = "";
                            vm.$emit('close');
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"),'warning',true,vm.$t('Alert'));
                        }
                         
                    });
                    }else{
                        var objectPostString = JSON.stringify(data);
                        DataService.AddEmailTemplate(objectPostString).then(function (response) {
                           
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t("Email Sent Successfully"),'success',true,vm.$t('Alert'));
                            vm.duedate = "";
                            vm.Subject = "";
                            vm.priority = "";
                            vm.Message = "";
                            vm.$emit('close');
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#Sendbulkmail').addClass('disabled');
                            $('#ChangeOwnerId').addClass('disabled');
                            $('#AdditionalOwnerId').addClass('disabled');
                            $('#CloseLeadId').addClass('disabled');
                            $('[id^=Delete]').addClass('disabled');
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"),'warning',true,vm.$t('Alert'));
                        }
                         
                    });
                    }

                }
            });

        },

    },

    }
</script>

<template>
    <transition name="modal">
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                       <slot name="header"><h5>{{$t("SendEmail")}}</h5></slot>
                       <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times" aria-hidden="true"></em></button> 
                    </div>
                    <div class="modal-body">
                       <Form ref="form"  v-slot="{valid,errors}">
                           <div class="">
                               <div class="row">
                                   <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group">
                                            <label> {{$t("Subject")}}:<span class="text-danger">*</span></label>
                                            <Field name="Email Subject" :rules="{'required':true}" v-slot="{errors}" >
                                            <input   type="text" v-bind:maxlength="50" v-model="emailSubject" name="emailSubject" v-bind:disabled="isView"  
                                            v-bind:class="{'form-control' : true,'is-invalid': errors[0]}" />
                                            <span   name="Email Subject" class="invalid-feedback">{{errors[0]}}</span>
                                                    </Field>    
                                        </div>
                                    </div>
                                   <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group">
                                           <label> {{$t("EmailTemplate")}}:<span class="text-danger">*</span></label>
                                           <Field  name="Email TemplateValue"  :rules="{'required':true}" label="Email" v-model="emailtemplateValue" v-slot="{errors}">
                                           <select id=""  name="emailtemplateValue"  v-bind:value="emailtemplateValue" v-on:change="getSelectedValue" v-bind:class="{'form-control' : true,'is-invalid': errors[0]}">
                                                <option value=""> {{$t("Select")}} {{ $t("EmailTemplate") }} </option>
                                                <option v-for="(temp,index) in emailtemplate" v-bind:value="temp.value" :key="index">{{temp.name}}</option>
                                           </select>
                                           <span   name="Email TemplateValue" class="invalid-feedback">{{errors[0]}}</span>
                                        </Field>                                                            
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                       <div class="form-group">
                                            <label> {{$t("Priority")}}:<span class="text-danger">*</span></label>
                                            <Field name="Email PriorityValue" :rules="{'required':true}"  v-slot="{errors}">
                                           <select id="emailPriorityValue"   name="emailPriorityValue"  v-model="emailPriorityValue" v-bind:class="{'form-control' : true,'is-invalid': errors[0]}">
                                                <option value=""> {{$t("Select") }} {{$t("Priority")}}  </option>
                                                <option value="normal">{{ $t("Normal")}}  </option>
                                                <option value="low"> {{$t("Low")}}  </option>
                                                <option value="high"> {{$t("High")}}  </option>  
                                           </select>  
                                           <span  v-if="errors[0]" name="Email PriorityValue"  class="invalid-feedback" >{{errors[0]}}</span>                                       
                                        </Field>
                                                  
                                        
                                     </div>
                                   </div>
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12" v-if="callFrom!='CampaignDetails'">
                                        <div class="form-group">
                                        <label> {{$t("Description")}}:<span class="text-danger">*</span></label>   
                                        <Field name="Description" :rules="{'required':true}"  v-slot="{errors}" > 
                                     
                                        <ckeditor  :editor="editor"  style="height: 400px " v-model="Description" ></ckeditor>       
                                         <span  v-if="errors[0]" name="Description" class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
  
                                        </div>
                                    </div>
                                </div>
                                <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <div class="py-2">
                                            
                                                <a href="javascript:;" class="btn btn-success formbtn text-white mr-2" v-on:click="saveEmail(valid,errors)"><em class="fa fa-send pr-2"></em>{{$t("Send")}}  </a>
                                                <a v-on:click="$emit('close')" class="btn btn-danger formbtn widthhalf text-white"><em class="fa fa-close pr-2"></em>{{$t("Cancel") }} </a>
                                            </div>
                                        </div>
                                        <div class="col-lg-6 pt-2">
                                            <small class="text-danger"> {{$t("MandatoryString")}}  </small>
                                        </div>
                                   </div>
                            </div>
                        </Form>
                    </div>                  
                </div>
            </div>
        </div>        
    </transition>
</template>